import { EntryData } from "../main";

export const entry: EntryData = {
    title: "Largest product in a series",
    async callback() {
        const interval = 13;
        const n = "";
        const splits = n.split("0").map(e => e.split("").map(d => parseInt(d, 10)));
        let greatest = 0;

        for (const split of splits) {
            let product = 1;
            for (let i = 0; i < split.length; i++) {
                if (i >= interval) {
                    product /= split[i-interval];
                }
                product *= split[i];
                greatest = Math.max(product, greatest);
            }
        }

        return greatest;
    }
}